.admin-pictures-list {
  display: grid;
  grid-template-columns: auto;
	&__image-goods{
		max-width: 100%;
		width: 100px;
		height: auto;
		object-fit: cover;
		margin-bottom: 10px;
		align-items: center;
		justify-content: center;
	}
	&__item-text{
		padding: 10px;
		font-size: 18px;
		flex-grow: 1;
		display: flex;
		align-items: center;
		text-align: center;
		border: 1px solid;
	}
	&__price{
		font-size: 25px;
		text-align: center;
		display: flex;
		justify-content: center;
	}
	&__phote-title{
		display: flex;
		flex-direction: column;
	}
	&__shift-btn{
		transform: translateY(3px);
	}
	&__greed{
		width: 100%;
		display: grid;
		grid-template-columns: repeat(8, minmax(100px, auto));
		border-radius: 10px;
		border: 1px solid;
		overflow: hidden;
		margin-bottom: 20px;
	}

  &__pointer {
    cursor: pointer;
  }

  &__popup {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.85);
    & > div {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 608px;
      height: 322px;
      font-weight: normal;
      font-size: 48px;
      line-height: 54px;
      text-align: center;
      color: #000000;
      background: #ffffff;
      border: 1px solid #000000;
      box-shadow: 4px 4px 5px rgba(0, 0, 0, 0.25);
      border-radius: 10px;
      & div {
        display: flex;
        justify-content: space-between;
        padding: 0 35px 35px 35px;
        & button {
          cursor: pointer;
          display: flex;
          align-items: center;
          justify-content: center;
          width: 242px;
          height: 82px;
          border: 1px solid #000000;
          box-sizing: border-box;
          box-shadow: 4px 4px 5px rgba(0, 0, 0, 0.25);
          border-radius: 10px;
          font-family: Monotype Corsiva;
          font-weight: normal;
          font-size: 30px;
          line-height: 34px;
          &:hover {
            background: #000000;
            border: 1px solid #000000;
            box-sizing: border-box;
            box-shadow: 4px 4px 5px rgba(0, 0, 0, 0.25);
            border-radius: 10px;
            color: #ffffff;
          }
        }
      }
    }
  }

  &__data {
    // display: grid;
    // grid-template-columns: 90px 245px 124px 325px 155px 105px 100px 115px 177px;
    // margin-bottom: 15px;
    
    // border-radius: 10px;
    // padding: 4px;
    // font-size: 24px;
    // line-height: 27px;
		border: solid #000 1px;
		display: flex;
	
    & div {
      display: flex;
      max-height: 90px;
      align-items: center;
      // white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      & img {
        max-width: 90px;
        height: 90px;
      }

      &:nth-child(n + 2):nth-child(-n + 8) {
        border-right: 1px solid #000;
      }
      & input {
        width: 52px;
        height: 52px;
        background: #ffffff;
        border: 1px solid #000000;
        box-sizing: border-box;
        border-radius: 10px;
      }
      &:nth-child(8) {
        display: flex;
        align-items: center;
        justify-content: center;
        // & input[type="checkbox"]:checked + label::before {
        //   background-color: brown;
        // }
      }
      &:nth-child(9) {
        display: flex;
        justify-content: space-around;
        & a {
          display: flex;
        }
      }
    }

    &__itemText {
      font-family: "Monotype Corsiva";
      font-weight: 400;
      font-size: 0.7rem;
      color: #000000;
    }
  }
  &__pagination-container {
    display: grid;
    grid-template-columns: 240px 240px 256px 240px 240px;
    width: 1224px;
    height: 56px;
    margin: 0 auto;
    & div {
      &:nth-child(1) {
        display: flex;
        justify-self: end;
      }
      &:nth-child(2) {
        margin: 0 auto;
      }
    }
  }
  &__pages {
    display: flex;
    justify-self: center;
  }
  &__number {
    display: flex;
    margin: 0 8px;
  }
}
