.embla__slide__inner--thumb {
  touch-action: manipulation;
  cursor: pointer;
  border: 0;
  outline: 0;
  margin: 0;
  padding: 0;
  height: 100px;
  width: 100px;
  background-color: transparent;
  position: relative;
  display: block;
  overflow: hidden;
  border-radius: 10px;
}

